document.addEventListener('DOMContentLoaded', () => {
  const menuItems = document.querySelectorAll('.menu-vertical__item--with-sub');

  menuItems.forEach((item) => {
    const link = item.querySelector('.menu-vertical__item-content');
    const subMenu = item.querySelector('.menu-vertical--sub');

    // Oculta el submenú al cargar
    if (subMenu) subMenu.style.display = 'none';

    // Agrega el evento de clic
    link.addEventListener('click', (event) => {
      event.preventDefault(); // Evita el comportamiento predeterminado del enlace
      if (subMenu) {
        // Alterna la visibilidad del submenú
        item.classList.toggle('open');
        subMenu.classList.toggle('open');
        subMenu.style.display =
          subMenu.style.display === 'none' ? 'block' : 'none';
      }
    });
  });
});
